import React, { useState, useEffect } from "react";
import { Products, Home, Navbar, Cart, Checkout } from "./components/index";
import {
  BrowserRouter as Router,
  HashRouter,
  Route,
  Switch,
} from "react-router-dom";
import commerce from "./lib/commerce";
const App = () => {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [categories, setCategories] = useState([]);
  const [order, setOrder] = useState({});
  const [error, setError] = useState("");
  const fetchProducts = async () => {
    const { data } = await commerce.products.list();
    setProducts(data);
  };
  const getCart = async () => {
    setCart(await commerce.cart.retrieve());
  };
  const addToCart = async (productId, quantity) => {
    const item = await commerce.cart.add(productId, quantity);
    setCart(item.cart);
  };
  const removeFromCart = async (productId, e) => {
    const item = await commerce.cart.remove(productId);
    setCart(item.cart);
  };
  const emptyCart = async () => {
    const item = await commerce.cart.empty();
    setCart(item.cart);
  };
  const increaseItemQty = async (productId, quantity) => {
    const item = await commerce.cart.update(productId, {
      quantity: quantity + 1,
    });
    setCart(item.cart);
  };
  const decreaseItemQty = async (productId, quantity) => {
    const item = await commerce.cart.update(productId, {
      quantity: quantity - 1,
    });
    setCart(item.cart);
  };

  const refreshCart = async () => {
    const newCart = await commerce.cart.refresh();
    setCart(newCart);
  };
  const handleCheckout = async (checkoutTokenId, newOrder) => {
    try {
      const order = await commerce.checkout.capture(checkoutTokenId, newOrder);

      setOrder(order);

      refreshCart();
    } catch (error) {
      setError(error.data.error.message);
    }
  };
  useEffect(() => {
    fetchProducts();
    getCart();
  }, []);
  return (
    <HashRouter>
      <Navbar cartItems={cart.total_items} />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/products">
          <Products
            products={products}
            addToCart={addToCart}
            categories={categories}
          />
        </Route>
        <Route exact path="/cart">
          <Cart
            cart={cart}
            removeFromCart={removeFromCart}
            emptyCart={emptyCart}
            increaseItemQty={increaseItemQty}
            decreaseItemQty={decreaseItemQty}
          />
        </Route>
        <Route exact path="/checkout">
          <Checkout
            cart={cart}
            order={order}
            handleCheckout={handleCheckout}
            error={error}
            refreshCart={refreshCart}
          />
        </Route>
      </Switch>
    </HashRouter>
  );
};

export default App;
