import { makeStyles } from "@material-ui/core/styles";

export default makeStyles(() => ({
  root: {
    minWidth: 345,
  },
  media: {
    height: 200,
    cursor: "pointer",
    transform: "scale(0.95)",
    transition: "0.2s ease",
    borderRadius: "5px",
  },
  content: {
    height: "50px",
  },
  action: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
  },
  actionButtons: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    width: "100%",
    alignItems: "center",
  },
  button: {
    width: "100%",
  },
}));
