import { makeStyles } from "@material-ui/core/styles";
import homeBg from "../../../assets/homeBg.jpg";
export default makeStyles((theme) => ({
  container: {
    width: "100%",
    height: "100vh",
    backgroundImage: `url(${homeBg})`,
    backgroundPosition: "center",
    backgroundRepeat: "no repeat",
    backgroundSize: "cover",
    filter: "grayscale(0.6) contrast(1.1)",
  },
  introText: {
    width: "800px",
    height: "500px",
    position: "absolute",
    left: "50%",
    top: "40%",
    transform: "translate(-50%, -40%)",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
  button: {
    marginTop: "50px",
    width: "130px",
    fontSize: "10px",
  },
  text: {
    color: "#fff",
    animation: `$glow 3000ms linear infinite`,
  },
  textMobile: {
    fontSize: "30px",
    color: "#fff",
    animation: `$glow 3000ms linear infinite`,
  },
  "@keyframes glow": {
    "0%": {
      textShadow:
        "0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073",
    },
    "50%": {
      textShadow:
        "0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6",
    },
    "100%": {
      textShadow:
        "0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073",
    },
  },
}));
