import { makeStyles } from "@material-ui/core/styles";

export default makeStyles(() => ({
  root: {
    maxWidth: 345,
    height: "100%",
    transition: "0.2s ease",
    backgroundColor: "#ccc",
  },
  rootHover: {
    maxWidth: 345,
    height: "105%",
    transition: "0.2s ease",
  },
  media: {
    height: 200,
    cursor: "pointer",
    transform: "scale(0.95)",
    transition: "0.2s ease",
    borderRadius: "5px",
  },
  mediaHover: {
    height: 200,
    cursor: "pointer",
    transition: "0.2s ease",
  },
  content: {
    height: "150px",
  },
  action: {
    display: "flex",
    justifyContent: "space-between",
  },
}));
