import React, { useState, useEffect } from "react";
import {
  Paper,
  Stepper,
  Step,
  StepLabel,
  Typography,
  Button,
} from "@material-ui/core";
import useStyles from "./styles";
import AdressForm from "./AdressForm/AdressForm";
import PaymentForm from "./PaymentForm/PaymentForm";
import commerce from "../../lib/commerce";
import { Link, useHistory } from "react-router-dom";
const Checkout = ({ cart, refreshCart, handleCheckout }) => {
  const classes = useStyles();
  const history = useHistory();
  const steps = ["Shipping Adress", "Payment Details"];
  const [activeStep, setActiveStep] = useState(0);
  const [checkoutToken, setCheckoutToken] = useState(null);
  const [shippingInfo, setShippingInfo] = useState({});
  const Form = () =>
    activeStep === 0 ? (
      <AdressForm
        checkoutToken={checkoutToken}
        nextActiveStep={nextActiveStep}
        toOrderReview={toOrderReview}
      />
    ) : (
      <PaymentForm
        shippingInfo={shippingInfo}
        checkoutToken={checkoutToken}
        previousStep={previousStep}
        handleCheckout={handleCheckout}
        nextActiveStep={nextActiveStep}
      />
    );
  let Confirmation = () => (
    <>
      <div>
        <Typography variant="h5" gutterBottom>
          Thank you for your purchase, {shippingInfo.firstName}
          {shippingInfo.lastName}!
        </Typography>
        <Typography variant="subtitle2" gutterBottom>
          Order ref: {shippingInfo.shippingOption}
        </Typography>
        <Typography variant="subtitle2">
          You will receive the confirmation on the email you provided:{" "}
          {shippingInfo.email}
        </Typography>
      </div>
      <br />
      <Button
        onClick={refreshCart}
        component={Link}
        variant="outlined"
        type="button"
        to="/"
      >
        Back to home
      </Button>
    </>
  );
  useEffect(() => {
    if (cart.id) {
      const generateToken = async () => {
        try {
          const token = await commerce.checkout.generateToken(cart.id, {
            type: "cart",
          });

          setCheckoutToken(token);
        } catch {
          if (activeStep !== steps.length) history.push("/");
        }
      };

      generateToken();
    }
  }, [cart]);

  const nextActiveStep = () => {
    setActiveStep((previousActiveStep) => previousActiveStep + 1);
  };
  const previousStep = () => {
    setActiveStep((previousActiveStep) => previousActiveStep - 1);
  };

  const toOrderReview = (data) => {
    setShippingInfo(data);
    nextActiveStep();
  };
  return (
    <>
      <div className={classes.toolbar} />
      <main className={classes.layout}>
        <Paper elevation={3} variant="outlined" className={classes.paper}>
          <Typography variant="h4" align="center">
            Checkout
          </Typography>
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map((step) => (
              <Step key={step}>
                <StepLabel>{step}</StepLabel>
              </Step>
            ))}
          </Stepper>
          {activeStep === steps.length ? (
            <Confirmation />
          ) : (
            checkoutToken && <Form />
          )}
        </Paper>
      </main>
    </>
  );
};

export default Checkout;
