import React from "react";
import CartItem from "./CartItems/CartItems";
import { Container, Typography, Button, Grid } from "@material-ui/core";
import { Link } from "react-router-dom";
import useStyles from "./styles";
const Cart = ({
  cart,
  removeFromCart,
  emptyCart,
  increaseItemQty,
  decreaseItemQty,
}) => {
  const classes = useStyles();
  const EmptyCart = () => (
    <Container style={{ width: "100%", height: "100vh" }}>
      <Typography variant="h4">
        <div className={classes.linksContainer}>
          <Link style={{ fontSize: "20px" }} to="/">
            Home
          </Link>
          <Link style={{ fontSize: "20px" }} to="/products">
            Products
          </Link>
        </div>
      </Typography>
    </Container>
  );
  const FilledCart = () => (
    <>
      <Grid
        container
        spacing={3}
        style={{
          margin: "auto",
          borderBottom: "1px solid #eee",
          width: "100%",
          height: "auto",
        }}
      >
        {cart.line_items.map((item) => (
          <Grid key={item.id} item>
            <CartItem
              item={item}
              removeFromCart={removeFromCart}
              increaseItemQty={increaseItemQty}
              decreaseItemQty={decreaseItemQty}
            />
          </Grid>
        ))}
      </Grid>
      <div className={classes.cartDetails}>
        <Typography variant="h4" gutterBottom>
          Subtotal: {cart.subtotal.formatted_with_symbol}
        </Typography>
        <div className={classes.cartActionBtns}>
          <Button
            type="button"
            size="large"
            variant="contained"
            onClick={emptyCart}
            color="secondary"
          >
            Empty Cart
          </Button>
          <Button
            component={Link}
            to="/checkout"
            type="button"
            size="large"
            variant="contained"
            style={{ backgroundColor: "#FFDB58", color: "#3377FF" }}
          >
            Checkout
          </Button>
        </div>
      </div>
    </>
  );
  return (
    <div>
      <Container>
        <div className={classes.toolbar} />
        <Typography variant="h4" gutterBottom style={{ color: "#151515" }}>
          {!cart.total_items ? "Your Cart is empty." : "Your Cart"}
        </Typography>
        {!cart.total_items ? <EmptyCart /> : <FilledCart />}
      </Container>
    </div>
  );
};

export default Cart;
