import React from "react";
import {
  Typography,
  IconButton,
  Card,
  CardMedia,
  CardActions,
  CardContent,
  Button,
} from "@material-ui/core";
import useStyles from "./style";
import DeleteSharpIcon from "@material-ui/icons/DeleteSharp";
const CartItem = ({
  item,
  removeFromCart,
  increaseItemQty,
  decreaseItemQty,
}) => {
  const classes = useStyles();
  return (
    <>
      <Card className={classes.root}>
        <CardMedia className={classes.media} image={item.media.source} />
        <CardContent className={classes.content}>
          <Typography variant="h6" gutterBottom color="textPrimary">
            {item.name}
          </Typography>
          <Typography variant="body2" color="textSecondary">
            Price per piece: {item.price.raw}
          </Typography>
        </CardContent>
        <CardActions className={classes.action}>
          <div className={classes.actionButtons}>
            <IconButton onClick={() => decreaseItemQty(item.id, item.quantity)}>
              -
            </IconButton>
            <Typography>{item.quantity}</Typography>
            <IconButton onClick={() => increaseItemQty(item.id, item.quantity)}>
              +
            </IconButton>
          </div>
          <Typography gutterBottom color="textSecondary">
            Spent on item: {item.quantity * item.price.raw}
          </Typography>
          <Button
            onClick={() => removeFromCart(item.id)}
            size="large"
            className={classes.button}
            color="secondary"
            variant="contained"
          >
            <DeleteSharpIcon />
          </Button>
        </CardActions>
      </Card>
    </>
  );
};

export default CartItem;
