import React, { useState, useEffect } from "react";
import {
  InputLabel,
  Select,
  MenuItem,
  Grid,
  Typography,
  Button,
} from "@material-ui/core";
import { useForm, FormProvider } from "react-hook-form";
import CustomTextField from "../CustomTextField";
import commerce from "../../../lib/commerce.js";
import useStyles from "../styles";
import { Link } from "react-router-dom";
const AdressForm = ({ checkoutToken, toOrderReview }) => {
  const [shippingCountries, setShippingCountries] = useState([]);
  const [shippingCountry, setShippingCountry] = useState("");
  const [shippingCities, setShippingCities] = useState([]);
  const [shippingCity, setShippingCity] = useState("");
  const [shippingOptions, setShippingOptions] = useState([]);
  const [shippingOption, setShippingOption] = useState("");
  const methods = useForm();
  const classes = useStyles();
  const countries = Object.entries(shippingCountries).map(([code, name]) => ({
    id: code,
    label: name,
  }));
  const cities = Object.entries(shippingCities).map(([code, name]) => ({
    id: code,
    label: name,
  }));
  const options = shippingOptions.map((option) => ({
    id: option.id,
    label: `${option.description} - (${option.price.formatted_with_code})`,
  }));
  const getCountries = async (checkoutTokenId) => {
    const { countries } = await commerce.services.localeListShippingCountries(
      checkoutTokenId
    );
    setShippingCountries(countries);
    setShippingCountry(Object.keys(countries)[0]);
  };

  const getCities = async (countryId) => {
    const regions = await commerce.services.localeListSubdivisions(countryId);
    const acRegions = regions.subdivisions;
    setShippingCities(acRegions);
    setShippingCity(Object.keys(acRegions)[0]);
  };
  const getShippingOptions = async (checkoutTokenId, country, region) => {
    const options = await commerce.checkout.getShippingOptions(
      checkoutTokenId,
      {
        country: country,
        region: region,
      }
    );
    setShippingOptions(options);
    setShippingOption(options[0].id);
  };
  useEffect(() => {
    getCountries(checkoutToken.id);
  }, []);

  useEffect(() => {
    if (shippingCountry) getCities(shippingCountry);
  }, [shippingCountry]);

  useEffect(() => {
    if (shippingCity) {
      getShippingOptions(checkoutToken.id, shippingCountry, shippingCity);
    }
  }, [shippingCity]);
  return (
    <>
      <Typography variant="h6" gutterBottom>
        Shipping Adress
        <FormProvider {...methods}>
          <form
            onSubmit={methods.handleSubmit((data) =>
              toOrderReview({
                ...data,
                shippingCountry,
                shippingOption,
                shippingCity,
              })
            )}
          >
            <Grid container spacing={3}>
              <CustomTextField required name="firstName" label="First Name" />
              <CustomTextField required name="lastName" label="Last Name" />
              <CustomTextField required name="email" label="Email" />
              <CustomTextField required name="phone" label="Phone Number" />
              <CustomTextField
                required
                name="adress1"
                label="Street, Apt/Suite/Unit"
              />
              <Grid item xs={12} sm={6}>
                <InputLabel>Shipping Country</InputLabel>
                <Select
                  value={shippingCountry}
                  fullWidth
                  onChange={(e) => setShippingCountry(e.target.value)}
                >
                  {countries.map((country) => (
                    <MenuItem key={country.id} value={country.id}>
                      {country.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
              <Grid item xs={12} sm={6}>
                <InputLabel>Shipping City</InputLabel>
                <Select
                  value={shippingCity}
                  fullWidth
                  onChange={(e) => setShippingCity(e.target.value)}
                >
                  {cities.map((city) => (
                    <MenuItem key={city.id} value={city.id}>
                      {city.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
              <Grid item xs={12} sm={6}>
                <InputLabel>Shipping Option</InputLabel>
                <Select
                  value={shippingOption}
                  fullWidth
                  onChange={(e) => setShippingOption(e.target.value)}
                >
                  {options.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
            </Grid>
            <div className={classes.btnGroup}>
              <Button
                component={Link}
                to="/cart"
                color="primary"
                variant="outlined"
              >
                Back to cart
              </Button>

              <Button type="submit" color="primary" variant="contained">
                Next
              </Button>
            </div>
          </form>
        </FormProvider>
      </Typography>
    </>
  );
};

export default AdressForm;
