import React from "react";
import { Typography, Button, Divider } from "@material-ui/core";
import {
  Elements,
  CardElement,
  ElementsConsumer,
} from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import OrderReview from "../OrderReview";
const stripePromise = loadStripe(process.env.REACT_APP_STRIPE);
const PaymentForm = ({
  checkoutToken,
  previousStep,
  shippingInfo,
  handleCheckout,
  nextActiveStep,
}) => {
  const handleSubmit = async (e, elements, stripe) => {
    e.preventDefault();
    if (!stripe || !elements) return;

    const cardElement = elements.getElement(CardElement);

    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: cardElement,
    });

    if (error) {
    } else {
      const orderData = {
        line_items: checkoutToken.live.line_items,
        customer: {
          firstname: shippingInfo.firstName,
          lastname: shippingInfo.lastName,
          email: shippingInfo.email,
        },
        shipping: {
          street: shippingInfo.address1,
          county: shippingInfo.City,
          country: shippingInfo.shippingCountry,
        },
        fulfillment: { shipping_method: shippingInfo.shippingOption },
        payment: {
          gateway: "stripe",
          stripe: {
            payment_method_id: paymentMethod.id,
          },
        },
      };
      handleCheckout(checkoutToken.id, orderData);
      nextActiveStep();
    }
  };
  return (
    <>
      <OrderReview checkoutToken={checkoutToken} />
      <Divider />
      <Typography variant="h6" gutterBottom style={{ margin: "20px 0" }}>
        PaymentMethod
      </Typography>
      <Elements stripe={stripePromise}>
        <ElementsConsumer>
          {({ elements, stripe }) => (
            <form onSubmit={(e) => handleSubmit(e, elements, stripe)}>
              <CardElement />
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  margin: "30px 20px 10px",
                }}
              >
                <Button variant="outlined" onClick={previousStep}>
                  Back
                </Button>
                <Button
                  type="submit"
                  variant="contained"
                  disabled={!stripe}
                  color="primary"
                >
                  Pay {checkoutToken.live.subtotal.formatted_with_code}
                </Button>
              </div>
              <Typography align="center" variant="body2" color="textSecondary">
                For testing purposes keep pressing 42
              </Typography>
            </form>
          )}
        </ElementsConsumer>
      </Elements>
    </>
  );
};

export default PaymentForm;
